import { useState, useEffect  } from 'react'
import { data } from './data';
import Course from './Course';
import { auth, db } from './firebase';
import { doc, getDoc, setDoc } from 'firebase/firestore';
import { Link } from 'react-router-dom';
import { useNavigate } from 'react-router-dom';

const Courses = () => {
  const navigate = useNavigate();
  const [searchQuery, setSearchQuery] = useState('');
  const [bookmarkedOnly, setBookmarkedOnly] = useState(
    localStorage.getItem('bookmarkedOnly') === 'true'
  );
  const [userFavorites, setUserFavorites] = useState({});
  const [courses, setCourses] = useState([]);


  useEffect(() => {
    const unsubscribe = auth.onAuthStateChanged(async (user) => {
      if (!user) {
        navigate('/login');
      } else {
        // Fetch the user's favorite courses
        const userDocRef = doc(db, 'users', user.uid, 'courses', 'bookmarks');
        const userDocSnapshot = await getDoc(userDocRef);
        if (userDocSnapshot.exists()) {
          const favoriteCourses = userDocSnapshot.data();
          setUserFavorites(favoriteCourses);
        } else {
          const initialStatus = {};
          data.forEach((course, index) => {
            initialStatus[String(course.page)] = false;
          });
          await setDoc(userDocRef, initialStatus);
          setUserFavorites(initialStatus);
        }
      }
    });

    return () => unsubscribe();
  }, [navigate]);

  useEffect(() => {
    // Filter the courses based on the search query and bookmarkedOnly state
    const filteredCourses = data.filter((course) => {
      const isInSearch = course.name.toLowerCase().includes(searchQuery.toLowerCase());
      const isBookmarked = userFavorites && userFavorites[course.page];
      return (!bookmarkedOnly || (bookmarkedOnly && isBookmarked)) && isInSearch;
    });

    setCourses(filteredCourses);
  }, [searchQuery, bookmarkedOnly, userFavorites]);

  useEffect(() => {
    // Save the bookmarkedOnly setting in localStorage
    localStorage.setItem('bookmarkedOnly', bookmarkedOnly);
  }, [bookmarkedOnly]);

  const coursesArr = courses.map((course) => (
    course.active ?
      <Link 
        to={`${course.page}`} key={course.id}>
        <Course
          key={course.id}
          name={course.name}
          img={course.image}
          active={course.active}
        />
      </Link>
    :
      <Course
        key={course.id}
        name={course.name}
        img={course.image}
        active={course.active}
      />
  ));

  return (
    <div className="w-auto min-h-[calc(100vh-65px)] max-h-fit bg-gray-100 text-black flex items-center overflow-hidden">
      {/* Courses */}
      <div className={'w-full max-w-6xl px-4 py-[5vh] mx-auto flex flex-col'}>
        <div className="mb-8">
          <h1 className="text-4xl font-bold border-b-4 border-purple-400 tracking-wider inline pb-1">
            Courses
          </h1>
        </div>
        <div className="w-full flex flex-col items-start">
          <input
            type="text"
            placeholder="Search Courses..."
            value={searchQuery}
            onChange={(e) => setSearchQuery(e.target.value)}
            className="bg-gray-200 text-black text-lg w-full px-4 py-2 rounded-xl"
          />
          <button
            onClick={() => setBookmarkedOnly(!bookmarkedOnly)}
            className={`${
              bookmarkedOnly ? 'bg-gray-500 text-white' : 'bg-gray-300/50 text-black hover:bg-gray-400/50'
            } px-4 py-1 rounded-xl duration-200 ease-linear text-lg font-bold mt-2`}
          >
          Show Bookmarked Courses Only
          </button>
        </div>
        <div className="w-full flex items-center justify-center">
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-10 mt-4 w-full
           mx-[10vw] md:mx-[3vw]">
            {coursesArr}
          </div>
        </div>
      </div>
    </div>
  )
}


export { Courses as default}