import React from 'react';

const Course = ({ name, img, active }) => {
  return (
    <div className={`w-full h-full bg-gray-100 hover:scale-105 duration-300 ease-in shadow-2xl shadow-gray-400 group 
    cursor-pointer rounded-2xl flex flex-col justify-center ${active ? 'cursor-pointer' : 'cursor-not-allowed'}`}>
      <div className=' h-1/3 aspect-square m-6 rounded-lg overflow-hidden'>
        <div className='bg-contain bg-center bg-no-repeat h-full'
          style={{ backgroundImage: `url(${img})` }}>
        </div>
      </div>
      <hr className='border-gray-300 mb-6' />
      <div className='mx-2 text-black'>
        <p className='text-xl font-bold text-center'>{name}</p>
      </div>
    </div>
  );
};

export { Course as default}